<template>
  <div id="gameCanvasContainer" class="container">
    <canvas ref="gameCanvas" class="responsive-canvas"></canvas>
  </div>
</template>

<script setup>
// コンポーネント
  import { ref, onMounted, onBeforeUnmount } from 'vue';
  import Phaser from 'phaser';
  import MyScene from './MyScene';

  const gameCanvasContainer = ref(null);
  const gameCanvas = ref(null);
  let game = null; 

  // ゲームを初期化する関数
  const initializeGame = () => {
    console.log('ゲームの初期化...');
    if (gameCanvas.value) {
      game = new Phaser.Game({
        type: Phaser.CANVAS,
        parent: gameCanvasContainer.value,
        canvas: gameCanvas.value,
        scene: MyScene,
        physics: {
          default: 'matter',
          matter: {
            gravity: { y: 0.5 }
          }
        },
        backgroundColor: '#ffffff'
      });
      resizeGame(); 
    } else {
      console.error('Canvasの参照が取得できません');
    }
  };

  // ゲームのサイズをリサイズする関数
  const resizeGame = () => {
    if (game) {
      const width = window.innerWidth / 2; 
      const height = window.innerHeight / 2; 

      // 確認用
      console.log(`キャンバスのサイズを ${width}px x ${height}px に変更します`);

      gameCanvas.value.width = width;
      gameCanvas.value.height = height;
      game.scale.resize(width, height);
    }
  };

  // Vueのライフサイクルフック
  onMounted(() => {
    initializeGame();
    window.addEventListener('resize', resizeGame); 
  });

  onBeforeUnmount(() => {
    window.removeEventListener('resize', resizeGame); 
    if (game) {
      game.destroy(true);
    }
  });

</script>

<style scoped>
  #gameCanvasContainer {
    width: 100%; 
    height: 100%;
  }

  .container{
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>
