<template>
  <div>
    <div class="header-container">
      <div class="left-section">
        <div class="timer">
          <p>Live中...</p>
          <div id="clock"></div>
        </div>
        <div class="MP3">
            <p>BGM {{ isPlaying ? 'ON' : 'OFF' }}</p>
            <img :src="bgm_icon" alt="BGM_icon"  @click="toggleBGM">
        </div>
      </div>
      <div class="SNS">
        <a href="https://www.youtube.com/@byfield-up7929" target="_blank" class="sns-icon" @click="BGMoff" @mouseover="playSound"><img :src="youtube_icon" alt="youtube_icon"></a>
        <a href="https://www.tiktok.com/@field_up_x?lang=ja-JP" target="_blank" class="sns-icon" @click="BGMoff" @mouseover="playSound"><img :src="tiktok_icon" alt="tiktok_icon"></a>
        <a href="https://www.facebook.com/" target="_blank" class="sns-icon" @click="BGMoff" @mouseover="playSound"><img :src="facebook_icon" alt="facebook_icon" ></a>
        <a href="https://lin.ee/famO2Sr" target="_blank" class="sns-icon" @click="BGMoff" @mouseover="playSound"><img :src="line_icon" alt="ラインアイコン"></a>
        <a href="https://www.instagram.com/" target="_blank" class="sns-icon" @click="BGMoff" @mouseover="playSound"><img :src="instagram_icon" alt="instagram_icon" ></a>
        <a href="https://twitter.com/FieldUP_X" target="_blank" class="sns-icon" @click="BGMoff" @mouseover="playSound"><img :src="x_icon" alt="x_icon"></a>
      </div>
      <div class="hamburger" @click="toggleMenu">
        <div :class="['openbtn8', { active: isMenuOpen }]">
          <div class="openbtn-area">
            <span></span><span></span><span></span>
          </div>
        </div>
      </div>
    </div>

    <MenuComponent 
      @menu_introduction_click="handleMenuClick('introduction')"
      @menu_VR_click="handleMenuClick('VR')"
      @menu_miniGame_click="handleMenuClick('miniGame')"
      @menu_Chat_click="handleMenuClick('Chat')"
      @menu_learning_click="handleMenuClick('learning')"
      @menu_Contact_click="handleMenuClick('Contact')" 
      @stop_audio="stopAudio"
      :class="{ open: isMenuOpen }" 
      :playSound="playSound"
      :okSound="okSound"
      :setSound="setSound"
      :decisionSound="decisionSound"
      :BGMoff="BGMoff"
      :toggleMenu="toggleMenu"
    />
  </div>
</template>

<script setup>
  import { ref, defineEmits, defineExpose, onMounted } from 'vue';
  import MenuComponent from './MenuComponent.vue';

  // 画像
  import bgm_icon from '@/assets/images/icons8-周波数-50 1.png';
  import youtube_icon from '@/assets/images/icons8-youtube-50.png';
  import tiktok_icon from '@/assets/images/icons8-tiktok-50 1.png';
  import facebook_icon from '@/assets/images/icons8-フェイスブック-50 1.png';
  import instagram_icon from '@/assets/images/icons8-インスタグラム-50 1.png';
  import x_icon from '@/assets/images/icons8-ツイッターx-50 1.png';
  import line_icon from '@/assets/images/icons8-line-50 1.png';

  import bgm from '@/assets/audio/ゲームオーバーじゃない.mp3';
  import setSE1 from '@/assets/audio/select.mp3';
  import decisionSE1 from '@/assets/audio/ok.mp3';
  import setSE2 from '@/assets/audio/get.mp3';
  import decisionSE2 from '@/assets/audio/se_kuzureru03.mp3';

  const isMenuOpen = ref(false);
  const isPlaying = ref(false);
  const audio = ref(null);


  // イベントを定義
  const emit = defineEmits([
    'menu_introduction_click',
    'menu_VR_click',
    'menu_miniGame_click',
    'menu_Chat_click',
    'menu_learning_click',
    'menu_Contact_click',
    'stop_audio',
  ]);

  const toggleMenu = () => {
    okSound();
    isMenuOpen.value = !isMenuOpen.value;
  };

  const handleMenuClick = (menuItem) => {
    emit(`${menuItem}_click`);
  };

  // 時計
  const updateClock = () => {
    const now = new Date();
    const hours = String(now.getHours()).padStart(2, '0');
    const minutes = String(now.getMinutes()).padStart(2, '0');
    
    const timeString = `${hours}:${minutes}`;
    
    const clockElement = document.getElementById('clock');
    if (clockElement) {
      clockElement.textContent = timeString;
    }
  };

  // コンポーネントがマウントされたときに時計を開始
  onMounted(() => {
    updateClock();
    setInterval(updateClock, 1000);
  });

  // SE
  const toggleBGM = () => {
    if (audio.value === null) {
      audio.value = new Audio(bgm);
      audio.value.volume = 0.05;
    }

    if (isPlaying.value) {
      audio.value.pause();
      audio.value.currentTime = 0;
      isPlaying.value = false;
    } else {
      audio.value.play();
      isPlaying.value = true;
    }
  };

  const BGMoff = () => {
    isPlaying.value = false;
    if (audio.value) {
      audio.value.pause();
      audio.value.currentTime = 0;
    }
  };

  const playSound = () => {
    if (isPlaying.value) {
      const hoverSound = new Audio(setSE1);
      hoverSound.volume = 0.15;
      hoverSound.play().catch(error => {
        console.error("Audio play failed: ", error);
      });
    }
  };

  const okSound = () => {
    if (isPlaying.value) {
      const hoverSound = new Audio(decisionSE1);
      hoverSound.volume = 0.1;
      hoverSound.play().catch(error => {
        console.error("Audio play failed: ", error);
      });
    }
  };

  const setSound = () => {
    if (isPlaying.value) {
      const hoverSound = new Audio(setSE2);
      hoverSound.volume = 0.05;
      hoverSound.play().catch(error => {
        console.error("Audio play failed: ", error);
      });
    }
  };

  const decisionSound = () =>  {
    if (isPlaying.value) {
      const hoverSound = new Audio(decisionSE2);
      hoverSound.volume = 0.02;
      hoverSound.play().catch(error => {
        console.error("Audio play failed: ", error);
      });
    }
  };

  // SEを親へ渡す
  defineExpose({
    BGMoff,
    playSound,
    okSound,
    setSound,
    decisionSound
  });
  
</script>

