          this.okSound();
        }
      },
      DecisionSound() {
        if (this.decisionSound) {
          this.decisionSound();
        }
      },
      SetSound() {
        if (this.setSound) {
          this.setSound();
        }
      },
      ToggleDefault() {
        if (this.toggleDefault) {
          this.toggleDefault();
        } else {
          console.error("toggleDefault function is not provided!");
        }
      },
