<template>
    <div class="cilp_container">
      <p>PlayList</p>
      <ul>
        <li v-for="(playlist, index) in playlists" :key="index" @mouseover="PlaySound" @click="OkSound">
          <a :href="playlist.url" target="_blank">{{ playlist.name }}</a>
        </li>
      </ul>
    </div>
  </template>
  
<script setup>
  import { ref, onMounted, defineProps } from 'vue';
  import { getPlaylists } from './youtube_api';
  
  // Propsの定義
  const props = defineProps({
    videoType: String,
    playSound: {
      type: Function,
      default: () => {}
    },
    okSound: {
      type: Function,
      default: () => {}
    },
    BGMoff: {
      type: Function,
      default: () => {}
    },
  });
  
  // データ定義
  const playlists = ref([]);
  
  // fetchPlaylistsメソッドの定義
  const fetchPlaylists = async () => {
    const data = await getPlaylists();
    if (props.videoType === 'main') {
      playlists.value = data.mainPlaylists;
    } else if (props.videoType === 'short') {
      playlists.value = data.shortPlaylists;
    }
  };
  
  // コンポーネントがマウントされたときにplaylistsを取得
  onMounted(() => {
    fetchPlaylists();
  });
  
  // SE関連のメソッド定義
  const PlaySound = () => {
    if (props.playSound) {
      props.playSound();
    }
  };
  
  const OkSound = () => {
    if (props.okSound) {
      props.okSound();
      if (props.BGMoff) {
        props.BGMoff();
      }
    }
  };
</script>
  
  
  

