// APIキーとチャンネルIDの定義、keyの切り替え
const channelId = 'UCGZ9tx2keMqtCSL_UW1vyRw';
const apiKeys = [
  "AIzaSyAvT3N_ma3-3tvGC2FRyZaS8AkS7RIviQ8",
  "AIzaSyDpuLlVXCDo7qU2TY_rSW8IyAfB3yAMH1M",
  "AIzaSyBs8t_8o-usJn0lZDelYZvokF3y-Pv5zI4",
];

let currentKeyIndex = 0;

function getApiKey() {
  return apiKeys[currentKeyIndex];
}
function switchApiKey() {
  currentKeyIndex = (currentKeyIndex + 1) % apiKeys.length;
}

// キャッシュとリクエスト間隔
const cache = {};
const LAST_REQUEST_TIME_KEY = 'lastRequestTime';
const REQUEST_INTERVAL = 60000; // 60秒
const CACHE_VERSION = 'v1'; // キャッシュバージョン管理

function canMakeRequest() {
  const lastRequestTime = localStorage.getItem(LAST_REQUEST_TIME_KEY);
  if (!lastRequestTime) return true;

  const now = Date.now();
  return now - lastRequestTime > REQUEST_INTERVAL;
}

function updateLastRequestTime() {
  localStorage.setItem(LAST_REQUEST_TIME_KEY, Date.now());
}

// APIリクエストの処理
async function fetchFromApi(url) {
  if (!canMakeRequest()) {
    console.warn('Request rate limit exceeded. Try again later.');
    return null;
  }

  const cacheKey = `${url}?v=${CACHE_VERSION}`;
  if (cache[cacheKey]) {
    return cache[cacheKey];
  }

  try {
    const response = await fetch(url);
    if (response.status === 403 || response.status === 429) {
      switchApiKey();
      const newApiKey = getApiKey();
      const newUrl = url.replace(/key=[^&]+/, `key=${newApiKey}`);
      const data = await fetchFromApi(newUrl);
      cache[cacheKey] = data;
      updateLastRequestTime();
      return data;
    }

    if (!response.ok) {
      console.error('API request failed with status:', response.status);
      return null;
    }

    const data = await response.json();
    cache[cacheKey] = data;
    updateLastRequestTime();
    return data;
  } catch (error) {
    console.error('Fetch error:', error);
    return null;
  }
}

// 動画データ
async function getLatest50Videos() {
  // api,idで
  const apiKey = getApiKey();
  const searchUrl = `https://www.googleapis.com/youtube/v3/search?part=snippet&channelId=${channelId}&order=date&maxResults=50&type=video&key=${apiKey}`;
  
  const searchData = await fetchFromApi(searchUrl);
  if (!searchData || !searchData.items) {
    console.error('キーワードと一致する動画がありません');
    return [];
  }

  return searchData.items.map(item => ({
    title: item.snippet.title,
    date: item.snippet.publishedAt,
    url: `https://www.youtube.com/watch?v=${item.id.videoId}`
  }));
}

// 動画データの分類
export async function getVideosData() {
  const allVideos = await getLatest50Videos();
  const mainVideos = [];
  const shortVideos = [];

  for (const video of allVideos) {
    if (video.title.includes('#ショート')) {
      shortVideos.push(video);
    } else {
      mainVideos.push(video);
    }
  }
  return {
    mainVideos,
    shortVideos
  };
}

// 再生リストデータの取得
async function getAllPlaylists() {
  const apiKey = getApiKey();
  const playlistsUrl = `https://www.googleapis.com/youtube/v3/playlists?part=snippet&channelId=${channelId}&maxResults=50&key=${apiKey}`;
  
  const playlistsData = await fetchFromApi(playlistsUrl);

  console.log('API Response:', playlistsData);
  if (!playlistsData || !playlistsData.items) {
    console.error('No playlists found or data is invalid.');
    return [];
  }

  return playlistsData.items.map(item => ({
    name: item.snippet.title,
    date: item.snippet.publishedAt,
    url: `https://www.youtube.com/playlist?list=${item.id}`
  }));
}

// 再生リストデータの分類
export async function getPlaylists() {
  const allPlaylists = await getAllPlaylists();
  const mainPlaylists = [];
  const shortPlaylists = [];

  for (const playlist of allPlaylists) {
    if (playlist.name.includes('ショート') || playlist.name.includes('#short')) {
      shortPlaylists.push(playlist);
    } else {
      mainPlaylists.push(playlist);
    }
  }
  return {
    mainPlaylists,
    shortPlaylists
  };
}


// キーワード検索
export async function getKeywordVideos(keyword) {
  // api,id
  const apiKey = getApiKey();
  const searchUrl = `https://www.googleapis.com/youtube/v3/search?part=snippet&q=${encodeURIComponent(keyword)}&channelId=${channelId}&order=date&maxResults=50&type=video&key=${apiKey}`;

  console.log(keyword);


  // データ取得
  const searchData = await fetchFromApi(searchUrl);

  console.log(searchData);

  // 一致する動画がない場合
  if (!searchData || !searchData.items || searchData.items.length === 0) {
    console.error('No videos found or data is invalid.');
    return {
      success: false,
      message: '動画が見つかりませんでした。'
    };
  }
  
  // 一致する動画がある場合
  return {
    success: true,
    data: searchData.items.map(item => ({
      title: item.snippet.title,
      thumbnailUrl: item.snippet.thumbnails.default.url, // サムネイルのURL
      url: `https://www.youtube.com/watch?v=${item.id.videoId}`
    }))
  };
}
