<template>
  <div v-if="isVisible" class="widget-container">
    <p>NEW YoutubeClip</p>
      <ul>
        <li v-for="(video, index) in videos.slice(0, 6)" :key="index" @mouseover="playSound" @click="okSound">
          <a :href="video.url" target="_blank">{{ video.date.split('T')[0] }} {{ video.title }}</a>
        </li>
      </ul>
  </div>
</template>

<script setup>
  import { ref, onMounted, defineProps } from 'vue';
  import { getVideosData } from './youtube_api.js';

  // Propsの定義
  const props = defineProps({
    videoType: String,
    playSound: {
      type: Function,
      default: () => {}
    },
    okSound: {
      type: Function,
      default: () => {}
    },
    BGMoff: {
      type: Function,
      default: () => {}
    },
  });

  // データの定義
  const videos = ref([]);
  const isVisible = ref(true);
  const isLoading = ref(false);
  const lastFetchTime = ref(null);

  // データを取得する関数
  const fetchVideos = async () => {
    const now = Date.now();
    if (lastFetchTime.value && now - lastFetchTime.value < 60000) {
      isLoading.value = false; // キャッシュがある場合もローディングを終了
      return;
    }

    isLoading.value = true; // データ取得中にローディングを表示

    const data = await getVideosData();
    videos.value = props.videoType === 'main' ? data.mainVideos : data.shortVideos;
    lastFetchTime.value = now;
    isLoading.value = false; // データ取得完了後にローディングを終了
  };

  // コンポーネントがマウントされたときにデータを取得
  onMounted(() => {
    fetchVideos();
  });

  // メソッドの定義


  // SE
  const playSound= () => {
    props.playSound();
  };

  const okSound= () => {
    props.okSound();
    props.BGMoff()
  };

</script>


