<template>
    <div>
        <div id="cursor" :style="cursorStyle"></div>
        <div id="stalker" :style="stalkerStyle"></div>
  </div>
</template>
  
<script setup>
  import { ref, computed, onMounted, onBeforeUnmount } from 'vue';
  
  // 状態を `ref` で管理
  const cursorX = ref(0);
  const cursorY = ref(0);
  const stalkerX = ref(0);
  const stalkerY = ref(0);
  const cursorActive = ref(false);
  
  // `computed` を使って動的なスタイルを計算
  const cursorStyle = computed(() => ({
    opacity: "0.9",
    top: `${cursorY.value}px`,
    left: `${cursorX.value}px`,
    transform: cursorActive.value ? "scale(1.4)" : "",
  }));
  
  const stalkerStyle = computed(() => ({
    opacity: "0.4",
    top: `${stalkerY.value}px`,
    left: `${stalkerX.value}px`,
    transform: cursorActive.value ? "scale(1.4)" : "",
  }));
  
  // マウスムーブイベントのハンドラ
  const handleMouseMove = (e) => {
    cursorX.value = e.clientX;
    cursorY.value = e.clientY;
    setTimeout(() => {
      stalkerX.value = e.clientX;
      stalkerY.value = e.clientY;
    }, 140);
  };
  
  // マウスがエレメントに入った時と出た時のハンドラ
  // const handleMouseEnter = () => {
  //   cursorActive.value = true;
  // };
  // const handleMouseLeave = () => {
  //   cursorActive.value = false;
  // };
  
  // `mounted` と `beforeUnmount` ライフサイクルフック
  onMounted(() => {
    document.addEventListener("mousemove", handleMouseMove);
  });
  
  onBeforeUnmount(() => {
    document.removeEventListener("mousemove", handleMouseMove);
  });
</script>
  
  
<style scoped>
  #cursor {
    position: fixed;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 0 10px 2px rgb(255, 255, 255);
    animation: glow-c 1.5s infinite alternate;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    margin: -10px 0 0 -10px;
    z-index: 9999;
    pointer-events: none;
    opacity: 0;
    transition: transform 0.1s;
  }
  @keyframes glow-c {
    0% {
        box-shadow: 0 0 5px 2px rgb(255, 255, 255);
    }
    100% {
        box-shadow: 0 0 20px 5px rgb(240, 255, 255);
    }
    }
  
  #stalker {
    position: fixed;
    background: transparent;
    border: 2px solid aqua;
    box-shadow: 0 0 10px 2px aqua;
    animation: glow-o 1.5s infinite alternate;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    border-radius: 30px;
    margin: -30px 0 0 -30px;
    z-index: 9998;
    pointer-events: none;
    opacity: 0;
    transition: transform 0.5s;
  }
  @keyframes glow-o {
    0% {
        box-shadow: 0 0 5px 2px aqua;
    }
    100% {
        box-shadow: 0 0 20px 5px aqua;
    }
    }
  
  </style>
  