  <div :class="['ham-menu', { open: isMenuOpen }]">
    <audio id="hover-sound" src="@/assets/audio/select.mp3" preload="auto"></audio>
    <div class="menu-container">
      <p class="menu">menu</p>
      <ul class="list">
        <li @mouseover="playSound" @click="Introduction" tabindex="0" aria-label="Introduction">Introduction</li>
        <li @mouseover="playSound" @click="VRexperience" tabindex="0" aria-label="VR experience">VR experience</li>
        <li @mouseover="playSound" @click="miniGame" tabindex="0" aria-label="Mini Game">miniGame</li>
        <li @mouseover="playSound" @click="Chat" tabindex="0" aria-label="Chat">Chat</li>
        <li @mouseover="playSound" @click="learning" tabindex="0" aria-label="Learning">learning</li>
        <li @mouseover="playSound" @click="Contact" tabindex="0" aria-label="Contact">Contact</li>
      </ul>
    </div>
    <div class="other-container">
      <p class="other">other</p>
      <ul class="list">
        <li @mouseover="playSound" @click="SiteLink" ><a href="https://www.field-up.work/" target="_blank" rel="noopener noreferrer">Main Site</a></li>
        <li @mouseover="playSound" @click="SiteLink" ><a href="https://www.field-up.work/mental_field-up/" target="_blank" rel="noopener noreferrer">MentalHealth Site</a></li>
        <li @mouseover="playSound" @click="SiteLink" ><a href="https://www.field-up.work/officeinterior/" target="_blank" rel="noopener noreferrer">Interior Site</a></li>
        <li @mouseover="playSound" @click="SiteLink" ><a href="https://www.field-up.work/field-up.system/" target="_blank" rel="noopener noreferrer">SistemDev Site</a></li>
        <li @mouseover="playSound" @click="SiteLink" ><a href="https://field-up.work/demo_room/main.html" target="_blank" rel="noopener noreferrer">VR site</a></li>
      </ul>
    </div>
  </div>
</template>

<script setup>
  import { defineProps, defineEmits } from 'vue';

  const props = defineProps({
    isMenuOpen: {
      type: Boolean,
      default: false
    },
    playSound: {
      type: Function,
      default: () => {}
    },
    okSound: {
      type: Function,
      default: () => {}
    },
    setSound: {
      type: Function,
      default: () => {}
    },
    decisionSound: {
      type: Function,
      default: () => {}
    },
    BGMoff: {
      type: Function,
      default: () => {}
    },
    toggleMenu: {
      type: Function,
      default: () => {}
    }
  });

  const emit = defineEmits(
    ['menu_introduction_click', 
    'menu_VR_click', 
    'menu_miniGame_click', 
    'menu_Chat_click', 
    'menu_learning_click', 
    'menu_Contact_click',
    'stop_audio',
  ]);

  function Introduction() {
    props.toggleMenu();
    emit('menu_introduction_click');
  }

  function VRexperience() {
    props.toggleMenu();
    emit('menu_VR_click');
  }

  const miniGame = () => {
    props.toggleMenu();
    emit('menu_miniGame_click');
  }

  function Chat() {
    props.toggleMenu();
    emit('menu_Chat_click');
  }

  function learning() {
    props.toggleMenu();
    emit('menu_learning_click');
  }

  function Contact() {
    props.toggleMenu();
    emit('menu_Contact_click');
  }

  function SiteLink() {
    props.okSound();
    props.BGMoff();
    emit('stop_audio');
  }
</script>

