<script setup>
// コンポーネント
  import { ref, nextTick } from 'vue';
  import HeaderComponent from './components/common/HeaderComponent.vue';
  import WidgetComponent from './components/common/WidgetComponent.vue';
  import VideoViewer from './components/common/VideoViewer.vue';
  import MiniGame from './components/mini_game/MiniGame.vue';
  import VRexperience from './components/VR/VRexperience.vue';
  import ChatView from './components/chat/ChatView.vue';
  import ContactView from './components/contact/ContactView.vue';
  import LoaderAnimeComponent from './components/common/LoaderAnime.vue';
  import CursorTracker from './components/common/CursorTracker.vue';
  import PlayListComponent from './components/common/PlayListComponent.vue';
  import LearningBtn from "./components/common/LearningBtn.vue";
  import ChangeBtn from "./components/common/ChangeBtn.vue";
  import ChatBtn from "./components/common/ChatBtn.vue";
  import IntroBox from './components/introduction/IntroBox.vue';
  import AbatarChange from './components/introduction/AbatarChange.vue';
  import VideoSerch from './components/learning/VideoSerch.vue';


  // 画像,動画
  import avatar_top from '@/assets/videos/TOP.mp4';
  import avatar_short from '@/assets/videos/short.mp4';
  import avatar_minigame from '@/assets/images/ミニ5 1.png';
  import game_icon from '@/assets/images/6.png';
  import vr_icon from '@/assets/images/sinnri.png';
  import avatar_vr from '@/assets/images/アバター16 1.png';
  

  // 参照と状態の定義
  const showLoader = ref(false); // 開発中false
  const showdefault = ref(true); // 開発中true
  const showMiniGame = ref(false);
  const showShort = ref(false);
  const showVR = ref(false);
  const showIntroduction = ref(false);
  const showChat = ref(false);
  const showLearning = ref(false);
  const showContact = ref(false);
  const currentPage = ref('');

  // DOM要素の参照
  const collapseRefs = ref({});
  const headerComponent = ref(null);

  // フェードイン
  const beforeEnter = (el) => {
    el.style.opacity = 0;
  };

  const enter = (el, done) => {
    el.offsetHeight; 
    el.style.transition = 'opacity 0.5s';
    el.style.opacity = 1;
    setSound();
    done();
  };

  const leave = (el, done) => {
    el.style.transition = 'opacity 0.5s';
    el.style.opacity = 0;
    done();
  };

  // ロードアニメ
  const onLoadComplete = () => {
    showLoader.value = false;
    showdefault.value = true;
  };

  const toggleDefault = () => {
    decisionSound();
    CollapseShort();
    CollapseMiniGame();
    CollapseIntroduction();

    setTimeout(() => {
      resetVisibility();
      showdefault.value = true;
    }, 3000);
  };

  // 各コンポーネント表示
  const toggleShort = () => {
    decisionSound();
    CollapseDefault();

    setTimeout(() => {
      resetVisibility();
      showShort.value = true;
    }, 3000);
  };

  const toggleMiniGame = () => {
    decisionSound();
    CollapseDefault();
    CollapseShort();

    setTimeout(() => {
      resetVisibility();
      showMiniGame.value = true;
    }, 3000);
  };

  const toggleIntroduction = () => {
    decisionSound();
    CollapseDefault();
    CollapseShort();

    setTimeout(() => {
      resetVisibility();
      showIntroduction.value = true;
    }, 3000);
  };

  const toggleLearning = () => {
    decisionSound();
    CollapseDefault();
    CollapseLearning();

    setTimeout(() => {
      resetVisibility();
      showLearning.value = true;
    }, 3000);
  };

  const toggleVR = () => {
    decisionSound();
    CollapseDefault();

    setTimeout(() => {
      resetVisibility();
      showVR.value = true;
    }, 3000);
  };

  const toggleChat = () => {
    decisionSound();
    CollapseDefault();

    setTimeout(() => {
      resetVisibility();
      showChat.value = true;
    }, 3000);
  };

  const toggleContact = () => {
    decisionSound();
    CollapseDefault();

    setTimeout(() => {
      resetVisibility();
      showContact.value = true;
    }, 3000);
  };

  // 標準常時
  const resetVisibility = () => {
    showdefault.value = false;
    showIntroduction.value = false;
    showMiniGame.value = false;
    showVR.value = false;
    showChat.value = false;
    showLearning.value = false;
    showContact.value = false;
    showShort.value = false;
  };

  // 崩壊アニメーション
  const collapseAnimation = async (element, delay) => {
    console.log(`Calling collapseAnimation for ${element} with delay ${delay}`);
    
    // DOM 更新を待つ
    await nextTick();
    
    // 指定した遅延時間後にアニメーションを適用
    setTimeout(() => {
      const el = collapseRefs.value[element];
      console.log('要素' + collapseRefs.value[element]);
      console.log(`Retrieved element for ${element}:`, el);
      
      if (el) {
        console.log(`Adding classes to ${element}`);
        el.classList.add('animate__animated', 'animate__hinge');
        console.log(`Animation applied to ${element}`);
      } else {
        console.warn(`Element with ref '${element}' not found.`);
      }
    }, delay);
  };


  // 各種崩壊アニメーションの適用関数
  const CollapseDefault = async () => {
    await nextTick();
    await collapseAnimation('collapse0', 0);
    await collapseAnimation('collapse1', 200);
    await collapseAnimation('collapse2', 400);
    await collapseAnimation('collapse3', 500);
    await collapseAnimation('collapse4', 600);
    await collapseAnimation('collapse5', 700);
  };

  const CollapseShort = async () => {
    await nextTick();
    await collapseAnimation('collapse10', 0);
    await collapseAnimation('collapse11', 200);
    await collapseAnimation('collapse12', 400);
    await collapseAnimation('collapse13', 500);
    await collapseAnimation('collapse14', 600);
    await collapseAnimation('collapse15', 700);
  };

  const CollapseMiniGame = async () => {
    await nextTick();
    await collapseAnimation('collapse20', 0);
    await collapseAnimation('collapse21', 300);
    await collapseAnimation('collapse22', 400);
    await collapseAnimation('collapse23', 500);
  };

  const CollapseIntroduction = async () => {
    await nextTick();
    await collapseAnimation('collapse30', 0);
  };

  const CollapseLearning = async () => {
    await nextTick();
    await collapseAnimation('collapse40', 0);
  };

  

  // SE
  const BGMoff = () => {
    headerComponent.value.BGMoff();
  };
  const playSound = () => {
    headerComponent.value.playSound();
  };
  const okSound = () => {
    headerComponent.value.okSound();
  };
  const setSound = () => {
    headerComponent.value.setSound();
  };
  const decisionSound = () => {
    headerComponent.value.decisionSound();
  };
</script>



