<template>
  <transition name="fade-first" >
    <div v-if="loadingfirst" :class="{'loader': true, 'loader-fade-out': !loadingfirst}">
      <video 
        :src="videoSrc" 
        @ended="handleVideoEnd" 
        autoplay
        muted
        class="loading-video"
      ></video>
      <div class="loading-text silkscreen-bold">Loading...</div>
    </div>
  </transition>

  <transition name="fade-second" @before-enter="prepareSecondTransition" @after-enter="finishLoading">
    <div v-show="loadingsecond" :class="{'loader': true, 'loader-fade-out': !loadingsecond}">
      <div class="send-text silkscreen-bold">
        <span v-for="(char, index) in splitText" :key="index" :style="{ animationDelay: `${index * 0.1}s` }" class="char">
          {{ char }}
        </span>
      </div>
    </div>
  </transition>
</template>

<script setup>
import { ref, defineEmits } from 'vue';
import videoFile from '@/assets/videos/椅子で近づく.mp4';

// データ定義定義
const loadingfirst = ref(true);
const loadingsecond = ref(false);
const videoSrc = ref(videoFile);
const splitText = ref([]);

// メソッドの定義
const handleVideoEnd = () => {
  console.log('Video ended');
  startSecondTransition();
  loadingfirst.value = false;
};

const startSecondTransition = () => {
  console.log('Starting second transition');
  loadingsecond.value = true;
  splitText.value = "want to learn?".split('').map(char => char === ' ' ? '\u00A0' : char);
};

const prepareSecondTransition = () => {
  console.log('Preparing second transition');
};

const finishLoading = () => {
  console.log('Finishing loading');
  setTimeout(() => {
    console.log('Timeout completed');
    loadingsecond.value = false;
    emit('loaded'); // 親コンポーネントにイベントを送信
  }, 2500);
};

// `emit` 関数は `setup` 内で `defineEmits` を使って定義します
const emit = defineEmits(['loaded']);
</script>


<style scoped>
/* フォントデータ */
.silkscreen-bold {
  font-family: "Silkscreen", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.loader {
  position: fixed;
  width: 100%;
  height: 100vh;
  background-color: #000000;
  z-index: 99999;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  transition: opacity 0.8s ease-out; 
  overflow: hidden; 
}

.loading-video {
  width: 100%;
  height: 100%;
  object-fit: contain; 
}

.loading-text {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  color: #ffffff;
  font-size: 4rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7); 
  font-family: "Silkscreen", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.send-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #ffffff;
  font-size: 4rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  font-family: "Silkscreen", sans-serif;
  font-weight: 400;
  font-style: normal;
  
  white-space: nowrap; 
}
.char {
  display: inline-block;
  opacity: 0;
  transform: translateY(20px);
  animation: textAnimation 0.8s ease-out forwards;
}
@keyframes textAnimation {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.animate-text {
  animation: textAnimation 0.8s ease-out forwards;
}

@keyframes textAnimation {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%) translateY(80px);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, -50%) translateY(0);
  }
}

.fade-second-enter-active, .fade-second-leave-active {
  transition: opacity 0.8s ease-out;
}
.fade-second-enter, .fade-second-leave-to {
  opacity: 0;
}

</style>
